    'use client'

    import { useEffect, useState, useRef } from 'react'
    import InfiniteScroll from 'react-infinite-scroll-component'

    export default function HorizontalScroll({ gameId, apiKey }) {
    const [allTags, setAllTags] = useState([])
    const [visible, setVisible] = useState([])
    const [hasMore, setHasMore] = useState(true)
    const [isAutoScrolling, setIsAutoScrolling] = useState(true)
    const scrollContainerRef = useRef(null)
    const autoScrollIntervalRef = useRef(null)

    useEffect(() => {
        const fetchTags = async () => {
        try {
            const res = await fetch(`https://api.rawg.io/api/games/${gameId}?key=${apiKey}`)
            const data = await res.json()
            setAllTags(data.tags || [])
            setVisible(data.tags.slice(0, 6))
        } catch (error) {
            console.log('Failed to fetch tags:', error)
        }
        }

        fetchTags()
    }, [gameId, apiKey])

    // Auto-scroll functionality
    useEffect(() => {
        const container = scrollContainerRef.current
        if (!container || !isAutoScrolling) return

        const startAutoScroll = () => {
        autoScrollIntervalRef.current = setInterval(() => {
            const scrollAmount = 2 // pixels per interval
            container.scrollLeft += scrollAmount

            // If we've reached the end, reset to start for continuous loop
            if (container.scrollLeft >= container.scrollWidth - container.clientWidth) {
            container.scrollLeft = 0
            }
        }, 50) // 50ms interval for smooth scrolling
        }

        startAutoScroll()

        return () => {
        if (autoScrollIntervalRef.current) {
            clearInterval(autoScrollIntervalRef.current)
        }
        }
    }, [isAutoScrolling, visible.length])

    // Pause auto-scroll on hover, resume on leave
    const handleMouseEnter = () => {
        setIsAutoScrolling(false)
        if (autoScrollIntervalRef.current) {
        clearInterval(autoScrollIntervalRef.current)
        }
    }

    const handleMouseLeave = () => {
        setIsAutoScrolling(true)
    }

    const fetchMoreTags = () => {
        if (visible.length >= allTags.length) {
        setHasMore(false)
        return
        }

        const nextBatch = allTags.slice(visible.length, visible.length + 4)
        setTimeout(() => {
        setVisible(prev => [...prev, ...nextBatch])
        }, 500)
    }

    // Manual scroll detection to pause auto-scroll temporarily
    const handleScroll = () => {
        if (isAutoScrolling) {
        setIsAutoScrolling(false)
        // Resume auto-scroll after 3 seconds of no manual interaction
        setTimeout(() => {
            setIsAutoScrolling(true)
        }, 3000)
        }
    }

    return (
        <div className="relative">
        {/* Control buttons */}
        <div className="flex justify-end mb-2 gap-2">
            <button
            onClick={() => setIsAutoScrolling(!isAutoScrolling)}
            className={`px-3 py-1 text-xs rounded ${
                isAutoScrolling 
                ? 'bg-red-500 text-white hover:bg-red-600' 
                : 'bg-green-500 text-white hover:bg-green-600'
            } transition`}
            >
            {isAutoScrolling ? 'Pause' : 'Play'}
            </button>
        </div>

        <div
            ref={scrollContainerRef}
            id="scroll-container"
            className="flex overflow-x-auto space-x-2 scrollbar-hide px-4 py-2 cursor-pointer"
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
            onScroll={handleScroll}
            style={{
            scrollBehavior: isAutoScrolling ? 'auto' : 'smooth'
            }}
        >
            <InfiniteScroll
            dataLength={visible.length}
            next={fetchMoreTags}
            hasMore={hasMore}
            loader={
                <div className="flex items-center justify-center px-4">
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-cyan-300"></div>
                </div>
            }
            scrollableTarget="scroll-container"
            horizontal={true}
            className="flex gap-2"
            >
            {/* Duplicate the tags for seamless looping */}
            {[...visible, ...visible].map((tag, idx) => (
                <button
                key={`${tag.id || tag.name}-${idx}`}
                className="px-4 py-2 rounded-full bg-cyan-900 text-cyan-300 hover:bg-cyan-800 transition whitespace-nowrap flex-shrink-0 transform hover:scale-105"
                onClick={() => console.log('Clicked:', tag.name)}
                >
                {tag.name}
                </button>
            ))}
            </InfiniteScroll>
        </div>

        {/* Auto-scroll indicator */}
        <div className="flex items-center justify-center mt-2 text-xs text-gray-500">
            <div className={`w-2 h-2 rounded-full mr-2 ${isAutoScrolling ? 'bg-green-400 animate-pulse' : 'bg-gray-400'}`}></div>
            Auto-scroll {isAutoScrolling ? 'ON' : 'OFF'} • Hover to pause • Manual scroll pauses for 3s
        </div>
        </div>
    )
    }